<template>
    <div class="friend-link">
        <a 
            v-for="(item,index) in linkList"
            v-text="item.name"
            :key="index"
            :href="item.href">
        </a>
    </div>
</template>
<script>
export default {
    props:{
        'linkList':Array
    }
}
</script>
<style>
    .friend-link{
        display: flex;
        flex-wrap: wrap;
        padding:15px;
        border-right:#fff 5px solid;
        background: rgba(0,0,0,.5);
    }
    .friend-link a{
        flex:1;
        color:#aaa;
        text-align: center;
        text-decoration: none;
        transition: color .5s;
    }
    .friend-link a:hover{
        color:#fff;
        text-decoration: underline;
    }
</style>



