<template>
    <a class="github-item" :href="href" :alt="title">
        <h3 v-text="title"></h3>
        <div v-text="summary" v-if="summary"></div>
    </a>
</template>
<script>
export default {
    props:['title','summary','href']
}
</script>
<style>
    .github-item{
        display: block;
        margin-bottom:15px;
        text-decoration: none;
        color:#fff;
        background:rgba(0,0,0,.5)
    }
    .github-item h3{
        padding:15px;
    }
    .github-item div{
        padding:15px;
        color:#aaa;
    }
    .github-item:hover{
        text-decoration:underline;
    }
</style>



