<template>
    <div class="article-item">
        <div class="article-item__ct">
            <div class="article-item__ct-hd" v-if="image">
                <img class="article-item__img" :src="image"/>
            </div>
            <div class="article-item__ct-bd">
                <h3 class="article-item__title" v-text="title"></h3>
                <div 
                    class="article-itme__summary" 
                    v-if="summary"
                    v-text="summary">
                </div>
                <div class="article-item__ft">
                    <a class="article-item__href" :href="href">阅读全文</a>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:['title','href','image','summary']
}
</script>
<style>
    .article-item{
        padding:5px;
        margin-bottom: 15px;
        background:rgba(0,0,0,.5);
        color:#f5f5f5;
        transition:all .5s cubic-bezier(0.075, 0.82, 0.165, 1);
    }
    .article-item:hover{
        transform: scale(1.05,1.05)
    }
    .article-item__ct{
        display: flex;
    }
    .article-item__ct-hd{
        margin-right:15px;
    }
    .article-item__ft{
        display: flex;
        justify-content: flex-end;
        padding:5px;
    }
    @media screen and (max-width:980px){
        .article-item__ct{
            display: block;
        }
        .article-item__img{
            width:100%;
            height:auto;
            opacity: 0.8;
        }
        .article-item__ct-hd{
            margin:0;
        }
        .article-itme__summary{
            color:#aaa;
        }
    }
</style>




