<script>
export default {
	data:_=>({
		articleList:require('#/page/Home/mock/articleList.js'),
		githubList:require('#/page/Home/mock/githubList.js'),
		friendList:require('#/page/Home/mock/friendList.js'),
		weekList:['星期日','星期一','星期二','星期三','星期四','星期五','星期六'],
		date:new Date()
	}),
    components:{
		ModuleItem:require('#/page/Home/component/Module.vue'),
		ArticleItem:require('#/page/Home/component/ArticleItem.vue'),
		GithubItem:require('#/page/Home/component/GithubItem.vue'),
		FriendList:require('#/page/Home/component/FriendLink.vue'),
	},
	computed:{
		m:function(){
			var value=this.date.getMonth();
			if(value<10){
				return '0'+value
			}
			return value
		},
		d:function(){
			var value=this.date.getDate();
			if(value<10){
				return '0'+value
			}
			return value
		},
	},
	methods:{
		// atv_moduleMounted(value){
		// 	var a=[].concat(this.$store.state.HOME.scrollTopList);
		// 	a.push(value);
		// 	a.sort((a,b)=>a-b);
		// 	this.$store.commit('COMMIT_TOP_LIST',a);	
		// }
	
	},
	mounted(){
	this.$nextTick(_=>{
			var a=document.getElementsByClassName('module-item');
			var b=[].concat(this.$store.state.HOME.scrollTopList);
			for(let i=0;i<a.length;i++){
				b.push(a[i].offsetTop);
			}
			b.sort((a,b)=>a-b);
			this.$store.commit('COMMIT_TOP_LIST',b);
		})
	},
}
</script>
<template>
    <div>
        <header class="home-header">
            <div class="home-header__left">
              	<p class="home-header__summary">学习能力强，探究欲望深，代码强迫症，前端发烧人</p>
              	<p class="home-header__summary">欢迎来到挺问原的小站</p>
            </div>
            <div class="home-header__right">
				<div class="home-header__date-md">{{`${m}.${d}`}}</div>
				<div class="home-header__date-yw">{{`${date.getFullYear()} ${weekList[date.getDay()]}`}}</div>
			</div>
        </header>
		<ModuleItem 
			title="文章列表" 
			:bottomHref="{text:'更多文章',href:'http://www.jianshu.com/u/fd817497ee3c'}" 
			id="ARTICLE_LIST">
			<div>
				<ArticleItem
					v-for="(item,index) in articleList"
					:key="index"
					:title="item.title"
					:image="item.imgSrc"
					:summary="item.summary"
					:href="item.contentUrl">
				</ArticleItem>
			</div>
		</ModuleItem>
		<ModuleItem 
			title="Github项目" 
			:bottomHref="{text:'gitHub地址',href:'https://github.com/Char-Ten'}" 
			id="GITHUB_PROJECT">
			<GithubItem
				v-for="(item,index) in githubList"
				v-if="!item.fork"
				:key="index"
				:title="item.name"
				:summary="item.description"
				:href="item.html_url">
			</GithubItem>
		</ModuleItem>
		<ModuleItem 
			title="友情链接" 
			id="FRIEND_LINK">
			<FriendList :linkList="friendList"></FriendList>
		</ModuleItem>
    </div>
</template>
<style>
.home-header{
	display: flex;
	width:100%;
	margin-bottom: 15px;
    color:#fff;
}
.home-header__left{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	flex:2;
	margin-right:15px;
	background:rgba(0,0,0,.5);
}
.home-header__right{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	flex:1;
	background:rgba(0,0,0,.5);
}
.home-header__date-md{
	margin-top:15px;
	font-size: 50px;
	font-weight: 100
}
.home-header__date-yw{
	margin-bottom:15px;
}
.home-header__summary{
    margin:15px 0;
    text-align: center;
}
</style>




