<template>
    <section ref="module-item" class="module-item">
        <header class="module-item__hd">
            <h2 :id="id?id:''">{{title}}</h2>
        </header>
        <section class="module-item__st">
            <slot></slot>
        </section>
        <footer class="module-item__ft" v-if="bottomHref">
            <a :href="bottomHref.href" v-text="bottomHref.text"></a>
        </footer>
    </section>
</template>
<script>
export default {
    props:['title','bottomHref','id'],
    mounted(){
    }
}
</script>
<style>
    .module-item{
        border-left: 1px solid #fff;
        margin-bottom:15px;
    }
    .module-item__hd{
        padding:15px;
        border-left:5px solid #fff;
        color:#fff;
        background:rgba(0,0,0,.5);
        
    }
    .module-item__hd h2{
        font-weight: 100;
    }
    .module-item__st{
        margin-top:15px;
        margin-left:15px;
    }
    .item{
        background:rgba(0,0,0,.5);
        color:#fff;
    }
    .module-item__ft{
        padding:5px;
        border-right: 5px solid #fff;
        margin-top:15px;
        background:rgba(0,0,0,.5);
        text-align: center;
        color:#fff;
    }
    .module-item__ft a{
        color:#fff;
    }
</style>



